
* {
  -webkit-tap-highlight-color: rgba(0,0,0,0);
}
#add-food {
  position: fixed;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 80%;
  height: 50%;
  background: #fff;
  box-shadow: 0px 4px 16px 2px rgba(0,0,0,0.5);
  border-radius: 6px;
  z-index: 100;
}
form {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.title {
  font-size: 1.5em;
  text-decoration: underline;
  padding: 0.6em;
}
.photo {
  box-shadow: inset 0 0 8px 0 rgba(0,0,0,.5);
  background: #fff;
  border: 3px solid #78fffa;
  border-radius: 6px;
  box-sizing: border-box;
  overflow: hidden;
}
.customFileButton {
  /*position: absolute;*/
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  width: 250px;
  height: 250px;
  font-size: 40px;
  /*box-shadow: inset 0px 0px 8px 0px rgba(0,0,0,0.5);
  background: #fff;
  border: 3px solid #78fffa;
  border-radius: 6px;
  box-sizing: border-box;*/
}
.customFileButton > img {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.fa-camera {
  color: #78fffa;
}
.submitButton {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 42px;
  background: #78FFFA;
  color: #fff;
  font-size: 20px;
  font-weight: bold;
  text-shadow: 0 0 4px #888;
  border: 0;
  border-radius: 0 0 6px 6px;
  -webkit-appearance: none;
}
.selected {
  background: #78fffa !important;
}
.vdp-datepicker__calendar .cell.selected, .vdp-datepicker__calendar .cell.selected.highlighted, .vdp-datepicker__calendar .cell.selected:hover {
  background: #78fffa !important;
}
input[type="text"] {
  padding: 10px;
  font-size: 14px;
  border: none;
  border-bottom: solid 2px #c9c9c9;
  transition: border 0.3s;
  outline: none;
  border-radius: 0;
  width: 160px;
}
input[type="text"]:focus,
input[type="text"].focus {
  border-bottom: solid 2px #78FFFA;
}

